<template>
  <div id="category">
    <h1>{{this_category}}</h1>
    <div class="category__container">
      <ul>
        <li v-for="(item, index) in category_products"
        :key="index">
          <a href="#"
          @click="go_page(`front/online_store/${$route.params.category}/${item.id}`)">
            <div class="img">
              <img src="../../assets/icon/new_tag.png" alt=""
              class="img__is_new"
              v-if="item.is_new">
              <img :src="item.images.main" alt=""
              class="img__product">
            </div>
            <div class="main">
              <p class="main__title">
                {{item.title}}
              </p>
              <p class="main__id">
                {{item.id}}
              </p>
              <hr class="main__hr">
              <p class="main__price">
                NT$ {{to_numberStr(item.price)}}
              </p>
            </div>
          </a>
        </li>
      </ul>
    </div>

  </div>
</template>

<script>
export default {
  watch: {
    $route() {
      this.create_list();
    },
  },
  props: ['products', 'category'],
  data() {
    return {
      this_category: '',
      category_products: [],
      // {
      //   title: '',
      //   id: '',
      //   price: 0,
      //   is_new: true,
      //   on_banner: true,
      //   category: [],
      //   images: {
      //     main: '',
      //     banner: '',
      //     others: [],
      //   },
      //   content: '',
      //   description: {
      //     title: '',
      //     text: '',
      //   },
      // },
    };
  },
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    create_list() {
      if (this.$route.params.category === 'all') {
        this.this_category = '全部';
        this.category_products = this.products;
      } else {
        this.this_category = this.category
          .find((item) => item.english === this.$route.params.category)
          .chinese;
        this.category_products = this.products
          .filter((item) => JSON.stringify(item.category)
            .includes(this.$route.params.category));
      }
    },
    to_numberStr(num) {
      const numStr = num.toString().split('.')[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',').toString();
      return numStr;
    },
  },
  created() {
    this.create_list();
  },
};
</script>
